/*
Theme Name: useji-child
Version: 1.0
Description: A child theme of Clean by MyThemeShop
Template: mts_clean
Text Domain: clean
*/
/* ------------------------------------------------------
Site Utility
--------------------------------------------------------- */
@font-face {
	font-family: 'icomoon';
	src: url("fonts/icomoon.eot?531r0y");
	src: url("fonts/icomoon.eot?531r0y#iefix") format("embedded-opentype"), url("fonts/icomoon.ttf?531r0y") format("truetype"), url("fonts/icomoon.woff?531r0y") format("woff"), url("fonts/icomoon.svg?531r0y#icomoon") format("svg");
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: never;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon-shopping-cart:before {
	content: "\e902";
}

.icon-heart-o:before {
	content: "\e900";
}

.icon-user-circle-o:before {
	content: "\e901";
}

/* ------------------------------------------------------
Site Header section
--------------------------------------------------------- */
.single-product #site-header {
	border-bottom: 0;
}

#site-header {
	border-bottom: 1px solid rgba(0, 0, 0, 0.12);
	float: none;
}

#site-header * {
	float: none;
}

#site-header .container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1.875rem 0.9375rem 20px;
}

#site-header::before, #site-header::after {
	display: none;
}

/* ------------------------------------------------------
Pimary Site Navigation
--------------------------------------------------------- */
#primary-navigation .navigation .menu {
	display: flex;
}

#shop-navigation .shop-menu {
	display: flex;
}

#shop-navigation .shop-menu li {
	margin-right: 15px;
}

#shop-navigation .shop-menu li:last-child {
	margin-right: 0;
}

#shop-navigation a {
	color: #20303c;
}

#shop-navigation a:focus, #shop-navigation a:hover {
	color: #a749ff;
}

.woo-icon {
	font-size: 0;
}

.woo-icon .fa {
	font-size: 1.25rem;
}

/* ------------------------------------------------------
Woocommerce Breadcrumbs
--------------------------------------------------------- */
.custom-breadcrumb {
	background-color: #f2f2f2;
	padding-top: 0.9375rem;
	padding-bottom: 0.9375rem;
}

.woocommerce .woocommerce-breadcrumb {
	font-size: 0.75em !important;
	margin-bottom: 0 !important;
}

/* ------------------------------------------------------
Woocommerce styles
--------------------------------------------------------- */
.single-product #page .article {
	width: 100%;
}

.woocommerce .quantity .qty .quantity .qty {
	border-radius: 0 !important;
}

.woocommerce a.button,
.woocommerce-page a.button,
.woocommerce button.button,
.woocommerce-page button.button,
.woocommerce input.button,
.woocommerce-page input.button,
.woocommerce #respond input#submit,
.woocommerce-page #respond input#submit,
.woocommerce #content input.button,
.woocommerce-page #content input.button {
	border-radius: 0 !important;
}

/* ------------------------------------------------------
Homepage Page Template Styles
--------------------------------------------------------- */
.page-template-page-home #page {
	padding-top: 2.5rem;
}

.page-template-page-home article header {
	float: none;
}

.page-template-page-home article header .entry-title {
	font-size: 0;
	color: transparent;
	margin: 0;
}

/* ------------------------------------------------------
Homepage Grid
--------------------------------------------------------- */
.home-grid {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-auto-rows: minmax(149px, auto);
	gap: 1.25rem;
	margin-bottom: 3.75rem;
}

.grid-item {
	background-color: #f0f1f2;
}

.grid-item figure {
	width: 100%;
	height: 100%;
	margin: 0;
	position: relative;
	overflow: hidden;
}

.grid-item figure img {
	width: 100%;
	height: auto;
	object-fit: cover;
	position: absolute;
	top: 0;
	left: 0;
}

.grid-item figcaption {
	background-color: #f0f1f2;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	text-align: center;
	z-index: 1;
}

.grid-item figcaption span {
	display: block;
}

.item-1 {
	grid-column: 1 / 3;
	grid-row: 1 / 4;
}

.item-2 {
	grid-column: 3 / 5;
	grid-row: 1 / 3;
}

.item-3 {
	grid-column: 3 / 4;
	grid-row: 3 / 6;
}

.item-4 {
	grid-column: 4 / 5;
	grid-row: 3 / 6;
}

.item-5 {
	grid-column: 1 / 3;
	grid-row: 4 / 6;
}

.item-6 {
	grid-column: 1 / 3;
	grid-row: 6 / 8;
}

.item-7 {
	grid-column: 3 / 4;
	grid-row: 6 / 8;
}

.item-8 {
	grid-column: 4 / 5;
	grid-row: 6 / 8;
}

/* ------------------------------------------------------
Site Footer section
--------------------------------------------------------- */
#site-footer {
	background-color: #f0f1f2 !important;
	border-top: 0;
}
